<template>
  <div class="books">
    <h1>{{ msg }}</h1>
    Books total: {{stats.total}}<br>
    borrowed books: {{stats.borrowed}}<br>
    <br>
    <input v-model="search_expr" placeholder="search me">

    <table style="width:100%">
      <tr>
        <th>Title</th>
        <th>Author</th>
        <th>Condition</th>
        <th>Identity</th>
        <th>Borrower</th>
        <th></th>
      </tr>
      <tr v-for="b in books_filtered" :key="b.id" v-on:click="toggle_row(b)">
          <td bgcolor="#ADD8E6" v-if="is_contained(b, selected_rows)">{{b.title}}</td>
          <td v-else>{{b.title}}</td>

          <td bgcolor="#ADD8E6" v-if="is_contained(b, selected_rows)">{{b.author.name}} {{b.author.surename}}</td>
          <td v-else>{{b.author.name}} {{b.author.surename}}</td>

          <td bgcolor="#ADD8E6" v-if="is_contained(b, selected_rows)">{{b.condition}}</td>
          <td v-else>{{b.condition}}</td>

          <td bgcolor="#ADD8E6" v-if="is_contained(b, selected_rows)">{{b.identity}}</td>
          <td v-else>{{b.identity}}</td>

          <td bgcolor="#ADD8E6" v-if="is_contained(b, selected_rows)">{{b.borrowed_by}}</td>
          <td v-else>{{b.borrowed_by}}</td>

      </tr>
    </table>
  </div>
</template>

<script>
import axios from "axios";
axios.defaults.baseURL = "http://localhost:4000/api";

export default {
  name: "Books",
  props: {
    msg: String
  },
  data() {
    return {
      books: [],
      errors: [],
      search_expr: "",
      selected_rows: []
    };
  },
  created() {
    axios
      .get("/books")
      .then(response => {
        this.books = response.data;
      })
      .catch(e => {
        this.errors.push(e);
      });
  },
  computed: {
    stats: function() {
      var borrowed = [];
      for (var i = 0; i < this.books.length; i++) {
        var si = this.books[i];

        if (si.borrowed_by != null) {
          borrowed.push(si);
        }
      }
      return {
        total: this.books.length,
        borrowed: borrowed.length
      };
    },
    books_filtered: function() {
      if (this.search_expr.length > 0) {
        var books_filtered = [];
        var expr = this.search_expr.toLowerCase();

        for (var i = 0; i < this.books.length; i++) {
          var si = this.books[i];

          if (
            si.title.toLowerCase().includes(expr) ||
            si.author.name.toLowerCase().includes(expr) ||
            si.author.surename.toLowerCase().includes(expr) ||
            si.identity.toLowerCase().includes(expr)
          ) {
            books_filtered.push(si);
          }
        }
        return books_filtered;
      } else {
        return this.books;
      }
    }
  },
  methods: {
    is_contained: function(obj, list) {
      return list.some((listElement) => {
        return listElement === obj;
      });
    },
    remove_by_attr: function(arr, attr, value) {
      return arr.filter((element) => {
        return element[attr] !== value;
      })
    },
    toggle_row: function(row) {
      if (this.is_contained(row, this.selected_rows)) {
          this.selected_rows = this.remove_by_attr(
            this.selected_rows, "id", row.id
            )
      } else {
        this.selected_rows.push(row);
      }
    },
  }
};
</script>

