<template>
  <div class="students">
    <h1>{{ msg }}</h1>
    Students total: {{stats.total}}<br>
    Students with books: {{stats.borrowed}}<br>
    <input v-model="search_expr" placeholder="search me">

    <table style="width:100%">
      <tr>
        <th>Lastname</th>
        <th>Firstname</th>
        <th>PersonalId</th>
        <th>Email</th>
        <th>Books</th>
        <th></th>
      </tr>
      <tr v-for="s in students_filtered" :key="s.id" v-on:click="toggle_row(s)">

        <td bgcolor="#ADD8E6" v-if="is_contained(s, selected_rows)">{{s.surename}}</td>
        <td v-else>{{s.surename}}</td>

        <td bgcolor="#ADD8E6" v-if="is_contained(s, selected_rows)">{{s.name}}</td>
        <td v-else>{{s.name}}</td>

        <td bgcolor="#ADD8E6" v-if="is_contained(s, selected_rows)">{{s.personal_id}}</td>
        <td v-else>{{s.personal_id}}</td>

        <td bgcolor="#ADD8E6" v-if="is_contained(s, selected_rows)">{{s.email}}</td>
        <td v-else>{{s.email}}</td>

        <td bgcolor="#ADD8E6" v-if="is_contained(s, selected_rows)">{{s.books.length}}</td>
        <td v-else>{{s.books.length}}</td>
      </tr>
    </table>
  </div>
</template>

<script>
import axios from "axios";
axios.defaults.baseURL = "http://localhost:4000/api";

export default {
  name: "Students",
  props: {
    msg: String
  },
  data() {
    return {
      students: [],
      errors: [],
      search_expr: "",
      selected_rows: []
    };
  },
  created() {
    axios
      .get("/students")
      .then(response => {
        this.students = response.data;
      })
      .catch(e => {
        this.errors.push(e);
      });
  },
  computed: {
    stats: function() {
      var borrowed = [];
      for (var i = 0; i < this.students.length; i++) {
        var si = this.students[i];

        if (si.books.length > 0) {
          borrowed.push(si);
        }
      }
      return {
        total: this.students.length,
        borrowed: borrowed.length
      };
    },
    students_filtered: function() {
      if (this.search_expr.length > 0) {
        var students_filtered = [];
        var expr = this.search_expr.toLowerCase();

        for (var i = 0; i < this.students.length; i++) {
          var si = this.students[i];

          if (
            si.name.toLowerCase().includes(expr) ||
            si.surename.toLowerCase().includes(expr) ||
            si.email.toLowerCase().includes(expr)
          ) {
            students_filtered.push(si);
          }
        }
        return students_filtered;
      } else {
        return this.students;
      }
    }
  },
  methods: {
    is_contained: function(obj, list) {
      var i;
      for (i = 0; i < list.length; i++) {
        if (list[i] === obj) {
          return true;
        }
      }
      return false;
    },
    remove_by_attr: function(arr, attr, value) {
      var i = arr.length;
      while (i--) {
        if (
          arr[i] &&
          arr[i].hasOwnProperty(attr) &&
          (arguments.length > 2 && arr[i][attr] === value)
        ) {
          arr.splice(i, 1);
        }
      }
      return arr;
    },
    toggle_row: function(row) {
      if (this.is_contained(row, this.selected_rows)) {
        this.selected_rows = this.remove_by_attr(
          this.selected_rows,
          "id",
          row.id
        );
      } else {
        this.selected_rows.push(row);
      }
    }
  }
};
</script>

